<template>
<div class="user-login-wrap">
    <div class="Box">
         <!---->
        <div class="LRSBox" v-if="isSuccessLoginPage">
            <div class="title"><span></span></div>
            <p><span class="colHeader">{{userInfo.username}}</span></p>
            <p v-if="userInfo.username=='waltz'">
                <span class="colManager"></span>
                <router-link to="/textP"></router-link>
            </p>
            <p v-else><span class="colHeader"></span></p>
            <p><span class="colHeader" @click="loginOut"></span></p>
        </div>

        <!---->
        <div class="LRSBox" v-if="isShowLoginPage">
            <div class="title"><span></span></div>
            <div class="inputBox">
                <span class="colHeader"></span>
                <input type="text" v-model="username"/>
            </div>
            <div class="inputBox">
                <span class="colHeader"></span>
                <input type="password" v-model="password"/>
            </div>
            <div class="inputBox">
                <span class="colHeader" @click="toLogin"></span>
                <a @click="toRegisterPage"></a>
                <p class="warning textCenter"></p>
            </div>
        </div>

        <!---->
        <div class="LRSBox" v-if="isShowRegisterPage">
            <div class="title"><span></span></div>
            <div class="inputBox">
                <span class="colHeader"></span><input type="text" v-model="username"/>
            </div>
            <div class="inputBox">
                <span class="colHeader"></span><input type="password" v-model="password"/>
            </div>
            <div class="inputBox">
                <span class="colHeader"></span><input type="password" v-model="repassword"/>
            </div>
            <div class="inputBox">
                <span class="colHeader"  @click="toRegister"></span>
                <a @click="toLoginPage"></a>
                <p class="warning textCenter"></p>
            </div>
        </div>
    </div>
</div>
</template>
 
<script>
import carousel from '../components/carousel'
import axios from 'axios'
export default{
    props: [''],
    components: {
        carousel,
    },
    
    data() {
        return {
            username: '', 
            password: '',
            repassword: '',
            userInfo: '',
            isShowRegisterPage: false, //
            isShowLoginPage: true, //
            isSuccessLoginPage: false, //
        }
    },
    
    mounted(){
        // this.initData();
    },
    
    methods: {
        /**
         * 
         */
        toRegisterPage() {
            this.isShowLoginPage = false;
            this.isShowRegisterPage = true;
        },

        /**
         * 
         */
        toLoginPage() {
            this.isShowLoginPage = true;
            this.isShowRegisterPage = false;
        },

        /**
         * 
         */
        toLogin() {
            debugger
            let url = tripDoc.api + tripDoc.activeAPI.login_post;
            axios
            .post(url, {
                username: this.username,
                password: this.password
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    this.message = res.data.message;
                    this.userInfo = res.data.userInfo;
                    if(this.message == '') {
                        this.isSuccessLoginPage = true;
                        this.isShowLoginPage = false;
                    }
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        toRegister() {
            let url = tripDoc.api + tripDoc.activeAPI.register_post;
            axios
            .post(url, {
                username: this.username,
                password: this.password,
                repassword: this.repassword
            })
            .then( (res) => {
                debugger
                if(res.data.code == 0) {
                    if(this.message == '') {
                        this.isShowRegisterPage = false;
                        this.isShowLoginPage = true;
                    }
                    this.userInfo = res.data.userInfo;
                    this.$set(res.data, 'userInfo', res.data.userInfo)
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        loginOut() {
            // 
            let url = tripDoc.api + tripDoc.activeAPI.loginOut_get;
            axios
            .get(url, {})
            .then( (res) => {
                debugger
                if(!res.code){
                    window.location.reload();
                }
            })
            .catch( (err) => {
                console.log(err)
            })
        },

        /**
         * 
         */
        initData() {
            this.$http.get('/api/').then((response) => {
                console.log(response)
            })
        }
    },
    
    }
</script>
 
<style lang='less' scoped>
    .Box{
        width: 8rem;
        height: 8rem;
        background: #f5f5f5;
        margin: .625rem auto;
        padding-top: 0.2rem;
        .LRSBox{
            width: 7rem;
            height: 4rem;
            border: solid .0625rem #ccc;
            margin: 1rem auto;
            .title{
                height: 1rem;
                line-height: 1rem;
            }
            .inputBox{
                height: 0.6rem;
                line-height: 0.6rem;
                .colHeader{
                    width: 1.2rem;
                    display: inline-block;
                    text-align: right;
                }
            }
        }
    }
</style>



